#overviewParner {
  width: 100%;
  position: relative;
  margin-bottom: 10rem;
}

.healthcare-banner {
  width: 90%;
}

.overview-partner-container {
  width: 100%;
  position: relative;
  display: grid;
  grid-template-columns: 0.4fr 1fr 0.9fr;
}

.digital-partner-description {
  font-family: "Poppins", sans-serif !important;
  font-size: 16px;
  color: #000;
  padding-bottom: 1rem;
  text-align: justify;
}

.digital-overview-heading-sec {
  padding-bottom: 1rem;
}

.digital-healthcare {
  padding: 4rem 3rem;
  background-color: #f5f6f9;
  margin-right: 5px;
}

.healthcare-our-partner {
  padding: 4rem 3rem;
  background-color: #f5f6f9;
}

.overview-bg-grid-item {
  background-image: url("../../assets/images/healthcare-industries/Screen\ Shot\ 2019-08-26\ at\ 9.22.49\ PM\ \(1\).png");
  background-repeat: no-repeat;
  background-size: cover;
}

#healthCareAccordion {
  width: 100%;
  position: relative;
  margin-bottom: 10rem;
}

.healthcare-solution-description {
  font-family: "Poppins", sans-serif !important;
  font-size: 16px;
  color: #000;
  padding-bottom: 1rem;
  padding-top: 1rem;
  width: 70%;
  text-align: justify;
}

#healthcareTechnology {
  width: 100%;
  position: relative;
  margin-bottom: 10rem;
  background-image: url("../../assets/images/healthcare-industries/healthcare-banner.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: none;
}

.healthcare-technology-inner-sec {
  width: 100%;
  position: relative;
  padding: 5rem 0;
}

.healthcare-technology-sec {
  width: 100%;
  position: relative;
  background-image: linear-gradient(to right, #ffffffeb 25%, #2d2d3700 90%);
}

.healthcare-technology-description {
  font-family: "Poppins", sans-serif !important;
  font-size: 16px;
  color: #000;
  padding-bottom: 1rem;
  padding-top: 1rem;
  width: 38%;
}

.read-use-cases-btn {
  background: transparent;
  border: none;
  font-family: "Poppins", sans-serif !important;
  font-size: 18px;
  font-weight: 600;
  padding: 0;
  color: rgba(54, 128, 247, 1);
}

.healthcare-technology-head-section p {
  font-family: "Poppins", sans-serif !important;
  font-size: 18px;
  font-weight: 400;
  line-height: 1;
  padding-bottom: 5px;
  color: rgb(84, 84, 84);
}

.healthcare-technology-head-section span {
  font-family: "Poppins", sans-serif !important;
  font-weight: 700;
  font-size: 30px;
  line-height: 1.2;
  display: inline-block;
  background: linear-gradient(
    90deg,
    rgba(54, 128, 247, 1) 0%,
    rgba(240, 173, 71, 1) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

#healthcareProductSlider {
  position: relative;
  width: 100%;
  margin-bottom: 10rem;
  background-color: #f5f6f9;
  padding-top: 3rem;
  padding-bottom: 7rem;
}

.healthcare-product-inner-sec {
  position: relative;
  width: 100%;
}

.healthcare-product-heading-sec p {
  font-weight: 700;
  letter-spacing: 1px;
  display: inline-block;
  background: linear-gradient(
    90deg,
    rgba(54, 128, 247, 1) 0%,
    rgba(240, 173, 71, 1) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 30px;
  font-family: "Poppins", sans-serif !important;
}

.healthcare-product-heading-sec span {
  font-family: "Poppins", sans-serif !important;
  font-weight: 400;
  letter-spacing: 1px;
  display: inline-block;
  background: linear-gradient(
    90deg,
    rgba(54, 128, 247, 1) 0%,
    rgba(240, 173, 71, 1) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 30px;
  line-height: 1.2;
}

.healthcare-product-heading-sec {
  padding-bottom: 3rem;
}

.healthcare-product-inner-sec .chistory__hero {
  padding: 0;
}

.healthcare-product-inner-sec .chistory__thumbnail {
  background: transparent;
  border-bottom: none;
}

.healthcare-slider-head {
  padding-bottom: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 40px;
}

.healthcare-slider-head p {
  font-family: "Poppins", sans-serif !important;
  font-weight: 400;
  font-size: 14px;
  color: #3680f7;
}

.healthcare-slider-head span {
  font-family: "Poppins", sans-serif !important;
  font-weight: 600;
  font-size: 16px;
  color: #000;
  line-height: 1.2;
}

.healthcare-product-desc p {
  font-family: "Poppins", sans-serif !important;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.3;
}

.healthcare-product-inner-sec button {
  background: transparent;
  color: #3680f7;
  font-weight: 500;
}

.healthcare-product-inner-sec .learn-more-button-sec {
  padding-top: 0rem;
  padding-bottom: 0;
}

.healthcare-product-desc {
  display: flex;
  min-height: 175px;
  padding-right: 0.5rem;
}

.healthcare-product-inner-sec .chistory_control {
  bottom: -18%;
  left: 2%;
}

#readUseCase {
  position: relative;
  width: 100%;
  margin-bottom: 10rem;
}

.read-usecase-btn-sec {
  padding-top: 2rem;
}

.read-usecase-btn-sec button {
  background-color: transparent;
  border: none;
  font-family: "Poppins", sans-serif !important;
  font-weight: 500;
  color: #3680f7;
  font-size: 18px;
  padding: 0;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.read-usecase-btn-sec button i {
  font-size: 20px;
}

#healthcareBrand {
  position: relative;
  width: 100%;
  margin-bottom: 10rem;
}

.healthcare-brand-sec .feature-description-container {
  background-color: transparent;
}

.healthcare-brand-sec {
  width: 92%;
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  margin: 0 0 0 auto;
  gap: 0.5rem;
}

.healthcare-brand-heading-sec {
  width: 92%;
  margin: 0 0 0 auto;
}

.brand-promise-sec {
  background-image: url("../../assets/images/healthcare-industries/Screen\ Shot\ 2019-08-26\ at\ 9.22.49\ PM\ \(1\).png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  display: grid;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  grid-template-columns: 1fr 1fr;
  gap: 0.5rem;
}

.brand-promise-grid-items {
  background: #fff;
  width: 98.7%;
  display: flex;
  align-items: center;
  border: 2px solid rgb(128 128 128 / 15%);
  justify-content: center;
  height: 98%;
}

.brand-promise-grid-items:hover {
  border-image: linear-gradient(
    90deg,
    rgba(54, 128, 247, 1) 0%,
    rgba(240, 173, 71, 1) 100%
  );
  border-image-slice: 1;
  background: #f5f6f9;
}

.brand-promise-grid-items span {
  font-family: "Poppins", sans-serif !important;
  font-weight: 700;
  color: #000;
  font-size: 16px;
}

#customSolution {
  position: relative;
  width: 100%;
  margin-bottom: 10rem;
}

.custom-solution-inner-sec {
  position: relative;
  width: 100%;
}

.custom-solution-head p {
  font-size: 30px;
  font-family: "Poppins", sans-serif !important;
  padding-bottom: 1rem;
  display: inline-block;
  background: linear-gradient(
    90deg,
    rgba(54, 128, 247, 1) 0%,
    rgba(240, 173, 71, 1) 70%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  border-bottom: 2px solid rgb(128 128 128 / 15%);
}

.custom-solution-head p:hover {
  border-image: linear-gradient(
    90deg,
    rgba(54, 128, 247, 1) 0%,
    rgba(240, 173, 71, 1) 100%
  );
  border-image-slice: 1;
}

.healthcare-contact-sec p {
  font-size: 18px;
  font-family: "Poppins", sans-serif !important;
  font-weight: 600;
  color: #3680f7;
  display: flex;
  align-items: center;
  gap: 1rem;
  border: none;
}

.healthcare-contact-sec p i {
  color: rgba(240, 173, 71, 1);
  border: 2px solid #3680f7;
  padding: 3px;
}

.custom-solution-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.healthcare-contact-sec {
  transition: all 5s ease-in-out;
  border-bottom: 2px solid rgba(0, 0, 0, 0);
}

.healthcare-contact-sec:hover {
  border-image: linear-gradient(
    90deg,
    rgba(54, 128, 247, 1) 0%,
    rgba(240, 173, 71, 1) 100%
  );
  border-image-slice: 1;
}

@media screen and (max-width: 480px) {
  .overview-partner-container {
    grid-template-columns: 1fr;
  }

  .overview-bg-grid-item {
    display: none;
  }

  .digital-healthcare .stories-heading-sec {
    padding-left: 0;
  }

  .healthcare-solution-description {
    width: 96%;
  }

  .healthcare-technology-description {
    width: 81%;
  }

  .healthcare-technology-sec {
    width: 100%;
    position: relative;
    background-image: linear-gradient(to right, #ffffffeb 55%, #2d2d3700 90%);
  }

  .google-educator-sec .feature-description-container {
    padding: 3rem 0;
  }

  #readUseCase {
    margin-bottom: 5rem;
  }

  #healthcareTechnology {
    margin-bottom: 5rem;
  }

  #healthcareBrand {
    margin-bottom: 5rem;
  }

  .healthcare-brand-sec {
    grid-template-columns: 1fr;
  }

  .healthcare-brand-sec {
    width: 100%;
  }

  .healthcare-brand-heading-sec {
    width: 97%;
    margin: 0 0 0 auto;
  }

  .brand-promise-sec {
    height: 225px;
  }

  #customSolution {
    position: relative;
    width: 100%;
    margin-bottom: 5rem;
  }

  #overviewParner {
    width: 100%;
    position: relative;
    margin-bottom: 5rem;
  }
}
